

body {
  font-family: 'Arial', sans-serif;
  background-color: #0a192f;
  color: white;
  margin: 0;
  padding: 0;
}


nav {
  position: sticky;
  top: 0;
  background-color: rgba(10, 25, 47, 0.9);
  backdrop-filter: blur(10px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  z-index: 50;
}

nav h1 {
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: 0.05rem;
}

nav .tabs {
  display: flex;
  gap: 1rem;
}

nav button {
  padding: 10px 20px;
  border: none;
  background: transparent;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

nav button:hover,
nav button.active {
  background: white;
  color: #0a192f;
  border-radius: 6px;
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.2);
}


.dropdown {
  position: relative;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #0a192f;
  border: 1px solid gray;
  border-radius: 6px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  display: none;
}

.dropdown-menu.active {
  display: block;
}

.dropdown-menu button {
  width: 100%;
  text-align: left;
  padding: 10px;
  transition: all 0.3s ease;
}

.dropdown-menu button:hover {
  background-color: gray;
}


.services-section {
  padding: 4rem 2rem;
  text-align: center;
  background-color: #f8f9fa;
  border-radius: 10px;
  margin: 2rem auto;
  max-width: 1200px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.services-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  justify-content: center;
  margin-top: 2rem;
}


.service-card {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: left;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
}

.service-card h3 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  color: #112240;
}

.service-card p {
  font-size: 1rem;
  color: #333;
}


@media (max-width: 768px) {
  .services-section {
    padding: 2rem 1rem;
  }
  
  .services-container {
    grid-template-columns: 1fr;
  }
}

